.about {
  margin-top: 20px;
  align-self: center;
  text-align: left;
  width: 60%;
  border-radius: 15px 15px;
}

.about__title {
  margin: 0 0 40px 0;
  font-size: 1.8em;
}

.about__text {
  font-size: 1em;
}
@media screen and (max-width: 1080px) {
  .about {
    width: 90%;
  }
}
